<template>
  <nos-component
    ref="NOS_COMPONENT"
    :keypad="true"
    vendor=""
  >



      <table>
      <tr class="py-2">
        <td class="col-2">npkencrypt:on</td>
        <td class="col-10">
          <table>
          <tr class="py-2">
            <td class="col-2">text</td>
            <td class="col-4">
              <input
                type="text"
                v-model="state.modelValues.textValue1"
                id="text-01"
                npkencrypt="on"
                data-keypad-type="alpha"
                maxlength="5"
                data-keypad-next="__hide__"
              />
            </td>

            <td class="col-2">password</td>
            <td class="col-4">
              <input
                type="password"
                v-model="state.modelValues.passwordValue1"

                id="password-01"
                npkencrypt="on"
                data-keypad-type="alpha"
                data-keypad-theme="mobile"
                maxlength="10"
                data-keypad-next="__hide__"
              />
            </td>
          </tr>
          </table>
        </td>
      </tr>
      
 
      <tr class="py-2">
        <td class="col-2">확장 UI</td>
        <td class="col-10">
          <table>
          <tr class="py-2">
            <td class="col-2">CVC</td>
            <td class="col-4">
              <input
                type="text"
                v-model="state.modelValues.extendUiValue1"

                id="extend-ui-01"
                npkencrypt="on"
                data-keypad-show="div"
                data-keypad-extend-ui="cvc"
                maxlength="3"
                data-keypad-next="__hide__"
              />
            </td>

          </tr>
          </table>
        </td>
      </tr>
      </table>


      <table>
      <tr class="text-center">
        <td class="col-6">
          <button @click="doSerialize">암호화값 얻기</button>
        </td>
        <td class="col-6">
          <button @click="doDecrypt">복호화하기</button>
        </td>
      </tr>


      <tr class="text-center">
        <td class="col-12">
          <button @click="state.dynamicFields = !state.dynamicFields">동적 필드</button>
        </td>
      </tr>

      <tr class="py-2" v-if="state.dynamicFields">
        <td class="col-2">동적필드</td>
        <td class="col-10">
          <table>
          <tr class="py-2">
            <td class="col-2">text</td>
            <td class="col-4">
              <input
                type="text"
                v-model="state.modelValues.dynamicFields1"
                id="dynamic-01"
                npkencrypt="on"
              />
            </td>

            <td class="col-2">password</td>
            <td class="col-4">
              <input
                type="password"
                v-model="state.modelValues.dynamicFields2"

                id="dynamic-02"
                npkencrypt="on"
                data-keypad-type="alpha"
                data-keypad-theme="mobile"
              />
            </td>
          </tr>
          </table>
        </td>
      </tr>
      </table>


      <table>
        
      <tr>
        <td class="col-2">
          직렬화 문자열
        </td>
        <td class="col-10">
          {{ state.NOS_SERIALIZED }}
        </td>
      </tr>
      <tr>
        <td class="col-2">
          복호화 문자열
        </td>
        <td class="col-10">
          <span v-html="state.decryptDebug"></span>
        </td>
      </tr>

    </table>
  </nos-component>
</template>


<script>
const axios = require('axios');
import NOSComponent from "@/components/pluginfree/NOSComponent";
import { reactive, ref } from '@vue/reactivity';
import { onMounted, watch, nextTick } from '@vue/runtime-core';

export default {
  components: {
    "nos-component" : NOSComponent
  },
  setup() {
    
    const state = reactive({
      dynamicFields : false,
      modelValues : {},
      decryptDebug : "",
      NOS_SERIALIZED : "",
      NOS_INSTANCE : {}
    })

    const table = (id) => {
      state.NOS_INSTANCE.table(id)
      .then((data) => {
        alert(data);
      });
    }

    const replacement = (id) => {
      state.NOS_INSTANCE.replacement(id)
      .then((data) => {
        alert(data);
      });
    }

    const encrypt = (id) => {
      state.NOS_INSTANCE.encrypt(id)
      .then((data) => {
        alert(data);
      });
    }

    const doSerialize = () => {
      state.NOS_SERIALIZED = state.NOS_INSTANCE.toJSON();
    }

    const doDecrypt = () => {
      nextTick(() => {
        const serialized = state.NOS_INSTANCE.toJSON();
        const json = JSON.stringify(serialized);
        
        let params = {
            p : "q",
            m : "d",
            s : json
        };
        
        const method = "post";
        let data = params;

        if(method === "post") {
          const querystring = require('querystring');
          data =  querystring.stringify(params);
        }

        axios({
          method : "post",
          url : "http://192.168.2.114:8077/decrypt.do",
          params : (method !== "post" ? data : null),
          data: (method === "post" ? data : null),
          withCredentials: true
        })
        .then(function (response) {
          if (response.status === 200 && response.data) {
            state.decryptDebug = response.data;
          }
        }
        , function (err) {
          console.log('err', err)
        })
      });
    }

    const NOS_COMPONENT = ref(null);

    onMounted(() => {
      state.NOS_INSTANCE = NOS_COMPONENT;
      state.NOS_INSTANCE.init();
    })

    watch(() => state.dynamicFields, (val) => {
      console.log("---------------watch.dynamicFields---------------------", val);
      if(val) {
        state.NOS_INSTANCE.init();
      } else {
        state.NOS_INSTANCE.destroy("dynamic-01");
        state.NOS_INSTANCE.destroy("dynamic-02");
      }
    })

    return {
      state,
      NOS_COMPONENT,
      table,
      replacement,
      encrypt,
      doSerialize,
      doDecrypt
    }
  }
}
</script>

<style scoped>
input {
  width: 100px;
  padding: 10px;
  border:1px solid #333333;
  border-radius: 5px;
  margin-right: 5px;
}
button {
  padding: 10px;
}
table {
  width: 100%;
}
td {
  text-align: left;
  /* font-size: 11pt; */
}
.p-3 {
  padding: 5px;
}

input:disabled, input:read-only {
  background-color:gray;
}

.nppfs-active-button {
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    padding: 10px;
    border:1px solid #333333;
    border-radius: 5px;
    margin-right: 5px;
    width: 40px;
    height: 34px;
}
button.keyboard {
    background-image: url("../assets/icon_keyboard.png");
}
button.mouse {
    background-image: url("../assets/icon_mouse.png");
}
</style>