import { npCommon, npDefine } from './npCommon';
import npConfig from './nos.config.js';
import { defineAsyncComponent, markRaw } from 'vue';

export const npKeypad = class npKeypad {
  constructor(controller, options) {
    const _self = this;

    _self.controller = controller;
    _self.vue = controller.vue;
    this.options = Object.assign(
      {
        absoluteUse: false,
      },
      options || {},
    );

    _self.uuid = _self.options.uuid;
    _self.keyset = _self.options.keyset;
    _self.absoluteUse = _self.options.absoluteUse;

    _self.components = {};
    _self.isRunning = false;
  }

  isSupport() {
    return true;
  }

  init() {
    const _self = this;

    const elements = npKeypad.scanElements();

    for (let i = 0; i < elements.length; i++) {
      const element = elements[i];

      const data = element.props;
      const elm = element.el;
      const attr = data[npConfig.E2E_ATTR_NAME];
      if (attr) {
        const id = data.id;
        if (!id) {
          const msg = '입력양식에 id 속성이 정의되어 있어야 합니다.';
          console.log(msg);
          continue;
        }
        if (id in _self.components) {
          const msg = '이미 초기화되었거나 입력양식의 id 속성이 중복됩니다. 마지막의 입력양식으로 초기화됩니다.';
          console.log(msg);
        }

        const elementId = id;

        const useynInput = data['data-keypad-useyn-input'];
        const toggleButton = _self.vue.$parent.$refs[useynInput];

        if (!useynInput || !toggleButton) {
          if (!_self.absoluteUse) continue;
        }

        const extendui = data['data-keypad-extend-ui'];
        const keypadComponent =
          extendui && npConfig.KEYPAD_EXTEND_UI[extendui]
            ? markRaw(npConfig.KEYPAD_EXTEND_UI[extendui])
            : markRaw(defineAsyncComponent(() => import('./NOSKeypad')));
            
        _self.components[id] = {
          component: keypadComponent,
          id: elementId,
          ref: elementId,
          props: {
            input: elm,
            uuid: _self.uuid,
            keyset: _self.keyset,
            absoluteUse: _self.options.absoluteUse,
          },
          events: {
            update: (arg) => {
              elm.value = _self.text(id);
              npCommon.dispatchEvent(elm, 'input');
              npCommon.dispatchEvent(elm, 'change');
            },
            hideall: () => {
              _self.hideAll();
            },
          },
        };

        element.keydownEventListener = (e) => {
          if (_self.isUseYn(id)) {
            e.preventDefault();
            e.stopPropagation();
            return false;
          }
        };
        element.focusEventListener = (e) => {
          if (_self.isUseYn(id)) {
            this.clear(id);
            _self.hideAll();
            _self.visible(id, true);
          }
        };
        element.clickEventListener = (e) => {
          
        };

        elm.addEventListener('focus', element.focusEventListener);
        elm.addEventListener('click', element.clickEventListener);
        elm.addEventListener('keydown', element.keydownEventListener);

        // 토글버튼 초기화 및 활성화/비활성화
        if (useynInput && toggleButton) {
          if (!_self.absoluteUse) {
            element.useynEventListener = (e) => {
              _self.toggleUseYn(id);
              elm.focus();
            };

            toggleButton.disabled = false;
            if (npDefine.isMobileDevice()) {
              toggleButton.addEventListener(
                'touch',
                element.useynEventListener,
              );
            } else {
              toggleButton.addEventListener(
                'click',
                element.useynEventListener,
              );
            }

            toggleButton.classList.remove('mouse');
            toggleButton.classList.add('keyboard');
          } else {
            toggleButton.disabled = true;
            toggleButton.classList.remove('keyboard');
            toggleButton.classList.add('mouse');
          }
        }
      }
    }

    _self.isRunning = true;
    if (npConfig.DEBUG_MODE) console.log('마우스입력기 초기화 - 완료');

    return Promise.resolve();
  }

  start() {
    return Promise.resolve();
  }

  clean() {}

  toJSON() {
    const ret = {
      __E2E_UNIQUE__: this.uuid,
      __E2E_KEYPAD__: this.keyset.rsakey,
      __E2E_KEYPAD_EXT__: this.keyset.rsakey3rd,
    };

    Object.keys(this.components).forEach((id) => {
      if (this.vue && id in this.vue.$refs) {
        const keypad = this.vue.$refs[id];
        let value = {};
        if (keypad && Array.isArray(keypad)) {
          keypad.some((kpd) => {
            value = kpd.serialize();
            return true;
          });
        } else {
          value = keypad.serialize();
        }
        Object.assign(ret, value);
      }
    });
    return ret;
  }

  find(id) {
    let ret = null;
    const _self = this;
    const keypad = _self.components[id];
    const component = _self.vue.$refs[id];
    if (keypad && component) {
      if (Array.isArray(component)) {
        component.some((kpd) => {
          ret = kpd;
          return true;
        });
      } else {
        ret = component;
      }
    }
    return ret;
  }

  isUseYn(id) {
    let ret = false;
    const keypad = this.find(id);
    if (keypad && keypad.isUseYn()) {
      ret = true;
    }
    return ret;
  }

  toggleUseYn(id) {
    const _self = this;
    const keypad = _self.find(id);
    if (keypad) {
      if (!_self.absoluteUse) {
        if (keypad.isUseYn()) {
          keypad.setUseYn(false);
        } else {
          keypad.setUseYn(true);
        }
      } else {
        keypad.setUseYn(true);
      }
      const secukey = _self.controller.modules.securekey;
      if (secukey) secukey.resetColor(id);
    }
  }

  table(id) {
    const _this = this;
    return new Promise(function (resolve, reject) {
      const keypad = _this.find(id);
      if (keypad && _this.isUseYn(id)) {
        const json = _this.toJSON();
        const params = {
          p: 'p',
          m: 't',
          u: json.__E2E_UNIQUE__,
          r: json.__E2E_KEYPAD__,
          k: keypad.getInfo(),
        };
        
        npCommon.post(params).then(function (data) {
          resolve(data);
        });
      } else {
        reject(new Error('마우스입력기를 사용하지 않는 입력양식입니다.'));
      }
    });
  }

  replacement(id) {
    const _this = this;
    return new Promise(function (resolve, reject) {
      const keypad = _this.find(id);
      console.log("[*] : ", keypad);
      if (keypad && _this.isUseYn(id)) {
        const json = _this.toJSON();
        console.log("[*] : ", json);
        const params = {
          p: 'p',
          m: 'r',
          u: json.__E2E_UNIQUE__,
          r: json.__E2E_KEYPAD__,
          k: keypad.getInfo(),
          v: keypad.getHash(),
        };
        // console.log(params);

        npCommon.post(params).then(function (data) {
          resolve(data);
        });
      } else {
        reject(new Error('마우스입력기를 사용하지 않는 입력양식입니다.'));
      }
    });
  }

  encrypt(id) {
    const _this = this;
    return new Promise(function (resolve, reject) {
      const keypad = _this.find(id);
      if (keypad && _this.isUseYn(id)) {
        const json = _this.toJSON();
        const params = {
          p: 'p',
          m: 'c',
          u: json.__E2E_UNIQUE__,
          r: json.__E2E_KEYPAD__,
          k: keypad.getInfo(),
          v: keypad.getHash(),
        };

        npCommon.post(params).then(function (data) {
          resolve(data);
        });
      } else {
        reject(new Error('마우스입력기를 사용하지 않는 입력양식입니다.'));
      }
    });
  }

  static scanElements(){
    let ret = [];
    const inputs = document.querySelectorAll('input');

    if (inputs && inputs.length > 0) {
      inputs.forEach((elm) => {
        if (elm.__vnode) {
          ret.push(elm.__vnode);
        }
      });
    }

    return ret;
  }

  componentsArray() {
    const _self = this;
    const ret = [];

    for (const key in _self.components) {
      ret.push(_self.components[key]);
    }
    return ret;
  }

  text(id) {
    const _this = this;
    const value = _this.value(id);

    if (value) {
      return value.formatted;
    }
    return '';
  }

  value(id) {
    const keypad = this.find(id);
    return keypad ? keypad.val() : null;
  }

  visible(id, vis) {
    const keypad = this.find(id);
    if (keypad) {
      vis ? keypad.show() : keypad.hide();
    }
  }

  close(id) {
    const keypad = this.find(id);
    if (keypad) {
      keypad.close();
    }
  }

  // 다른 모든 키패드 숨기기
  hideAll() {
    Object.keys(this.components).forEach((id) => {
      this.visible(id, false);
    });
  }

  closeAll() {
    Object.keys(this.components).forEach((id) => {
      this.close(id);
    });
  }

  focus(id) {
    const keypad = this.find(id);
    if (keypad) {
      keypad.focus(true);
    }
  }

  destroy(id) {
    const _this = this;
    const keypad = this.find(id);
    if (id in _this.components && keypad) {
      const keypadid = _this.components[id];
      const len = _this.components.length;
      let i = len - 1;
      while (i >= 0) {
        const item = _this.components[i];
        if (item.id === keypadid) {
          console.log(item);
          _this.components.splice(i, 1);
        }
        i--;
      }
      delete _this.components[id];
    }
  }

  clear(id) {
    const keypad = this.find(id);
    if(keypad) {
      keypad.clear(true);
    }
  }
};