const nua = navigator.userAgent;
export const npDefine = new (function () {
  const nav = navigator.appName;
  const plt = navigator.platform.toLowerCase();
  function npos(txt) {
    return nua.indexOf(txt);
  }
  function nin(txt) {
    return nua.includes(txt);
  }
  function lnin(txt) {
    return nua.toLowerCase().includes(txt);
  }
  this.ie =
    (nav === 'Microsoft Internet Explorer' ||
      (nav === 'Netscape' && (nin('MSIE') || nin('Trident')))) &&
    !nin('QQBrowser');
  this.ie64 = this.ie && nin('Win64; x64');
  this.edge = npos('Mozilla') === 0 && (nin('Edge/') || nin('Edg/'));
  this.ff =
    nin('Firefox') &&
    npos('Mozilla') === 0 &&
    nav === 'Netscape' &&
    !nin('Navigator');
  this.ns = nin('Gecko') && nin('Navigator');
  this.b360 = nin('360Browser') && nin('Chrome') && nin('Safari');
  this.qq = nin('QQBrowser') && nin('Trident');
  this.sf = nin('Safari') && !nin('Chrome');
  this.op = nin('Opera') || nin('OPR/');
  this.cr =
    nin('Chrome') &&
    nin('Safari') &&
    !nin('OPR/') &&
    !nin('360Browser') &&
    !nin('Edg/') &&
    !nin('Edge/');
  this.win = plt.includes('win') && !nin('Windows Phone');
  //	this.win98 = nin("Windows 98") || nin("Win98");
  //	this.winme = nin("Windows ME");
  //	this.winnt40 = nin("Windows NT 4.0");
  //	this.win64 = (plt.indexOf('win64') != -1) && !lnin('windows phone');
  this.win9x =
    nin('Windows 98') ||
    nin('Win98') ||
    nin('Windows ME') ||
    nin('Windows NT 4.0') ||
    nin('Windows NT 5.0') ||
    nin('Windows 2000');
  this.winxp = nin('Windows NT 5.1');
  this.mac = nin('Mac');
  this.lnx64 = nin('Linux') && nin('x86_64');
  this.lnx32 = nin('Linux') && (nin('i386') || nin('i686'));
  this.lnx = nin('Linux');
  this.and = nin('Android');
  this.ios = nin('iPhone') || nin('iPod') || nin('iPad');
  this.iph = nin('iPhone');
  this.ipo = nin('iPod');
  this.ipa = nin('iPad');
  this.fdr = lnin('fedora');
  this.ubt = lnin('ubuntu');
  this.winphone = lnin('windows phone');
  this.winmob = plt === 'windows mobile';

  this.virtualMachine = false;

  this.isMobileDevice = function () {
    if (
      this.winmob ||
      this.winphone ||
      this.ipa ||
      this.ipo ||
      this.iph ||
      this.and
    )
      return true;
    return false;
  };

  this.isMetroUi = function () {
    if (!this.ie) return false;
    if (!this.bwVersion) return false;
    if (!npCommon.compareVersion(this.bwVersion, '10.0')) return false;
    let supported = null;
    try {
      supported = !!new window.ActiveXObject('htmlfile');
    } catch (e) {
      supported = false;
    }
    if (supported) return false;

    if (window.screen.availWidth !== window.outerWidth) return false;

    // alert("window.screen.width : [" + window.screen.width + "], window.screen.availWidth : [" + window.screen.availWidth + "], window.outerWidth : [" + window.outerWidth + "], window.innerWidth  : [" + window.innerWidth  + "]");

    return window.screen.availWidth === window.outerWidth;
  };

  this.isOldIe = function () {
    return (
      this.ie &&
      (npCommon.compareVersion('7.0', this.bwVersion) ||
        document.documentMode <= 7)
    );
  };

  this.isSupported = function (version) {
    if (this.isMobileDevice() && version.MOBILE === false) return false;
    if (this.virtualMachine && version.VIRTUALOS === false) return false;

    return this.isSupportedOs(version) && this.isSupportedBw(version);
  };

  this.isSupportedOs = function (version) {
    const osVersion = this.osVersion;
    if (npDefine.win && version.WIN.Support) {
      if (npDefine.win9x) {
        return false;
      }
      return npCommon.checkVersion(
        osVersion,
        version.WIN.Os.Min,
        version.WIN.Os.Max,
      );
    } else if (npDefine.mac && version.MAC.Support) {
      return npCommon.checkVersion(
        osVersion,
        version.MAC.Os.Min,
        version.MAC.Os.Max,
      );
    } else if (npDefine.lnx && version.LINUX.Support) {
      // return checkVersionLinux(version, npBaseCtrl.linuxOsType, npBaseCtrl.linuxOsVersion);
      return true;
    }

    return false;
  };

  this.isSupportedBw = function (ver) {
    if (!this.isSupportedOs(ver)) {
      return false;
    }

    let version = null;
    if (npDefine.win) {
      version = ver.WIN.Bw;
    } else if (npDefine.mac) {
      version = ver.MAC.Bw;
    } else if (npDefine.lnx) {
      version = ver.LINUX.Bw;
    }

    if (!npCommon.isNull(version)) {
      const bwVersion = npDefine.bwVersion;
      if (npDefine.ie && version.IE.Support) {
        return npCommon.checkVersion(bwVersion, version.IE.Min, version.IE.Max);
      } else if (npDefine.ff && version.FF.Support) {
        return npCommon.checkVersion(bwVersion, version.FF.Min, version.FF.Max);
      } else if (npDefine.cr && version.CR.Support) {
        return npCommon.checkVersion(bwVersion, version.CR.Min, version.CR.Max);
      } else if (npDefine.sf && version.SF.Support) {
        return npCommon.checkVersion(bwVersion, version.SF.Min, version.SF.Max);
      } else if (npDefine.edge && version.EG.Support) {
        return npCommon.checkVersion(bwVersion, version.EG.Min, version.EG.Max);
      } else if (npDefine.op && version.OP.Support) {
        return npCommon.checkVersion(bwVersion, version.OP.Min, version.OP.Max);
      } else if (npDefine.b360 && version.B360.Support) {
        return npCommon.checkVersion(
          bwVersion,
          version.B360.Min,
          version.B360.Max,
        );
      } else if (npDefine.qq && version.QQ.Support) {
        return npCommon.checkVersion(bwVersion, version.QQ.Min, version.QQ.Max);
      }
    }

    return false;
  };

  this.makeBrowserVersionCode = function () {
    function leftpad(val, len, ch) {
      let ret = val;
      if (ret.length < len) {
        for (let i = 0; i < len - val.length; i++) {
          ret = ch + ret;
        }
      } else if (val.length > len) {
        ret = val.substring(0, len);
      }
      return ret;
    }

    let result = '99-000-000';
    try {
      let major = '';
      let minor = '';

      const ver = npDefine.bwVersion;
      if (ver.includes('.')) {
        const temp = ver.split('.');
        major = leftpad(temp[0], 3, '0');
        minor = leftpad(temp[1], 3, '0');
      } else {
        major = leftpad(ver, 3, '0');
        minor = leftpad('000', 3, '0');
      }

      result = major + '-' + minor;
      if (npDefine.ie) {
        result = '10-' + result;
      } else if (npDefine.ff) {
        result = '20-' + result;
      } else if (npDefine.cr) {
        result = '30-' + result;
      } else if (npDefine.sf) {
        result = '40-' + result;
      } else if (npDefine.op) {
        result = '50-' + result;
      } else if (npDefine.edge) {
        result = '60-' + major + '-000'; // Edge 브라우저는 . 뒤의 숫자는 버림(빌드버전으로 사료됨)
        // result = "60-" + result;
      } else if (npDefine.b360) {
        result = '91-' + result;
      } else if (npDefine.qq) {
        result = '92-' + result;
      } else {
        result = '99-000-000';
      }
    } catch (e) {
      result = '99-000-000';
    }
    return result;
  };
})();

npDefine.osVersion = (function () {
  let version = null;
  const ua = nua;
  if (npDefine.win) {
    const css = [
      { v: '5.0', p: /(Windows NT 5.1|Windows XP)/ },
      { v: '5.2', p: /Windows NT 5.2/ },
      { v: '6.0', p: /Windows NT 6.0/ },
      { v: '7.0', p: /(Windows 7|Windows NT 6.1)/ },
      { v: '8.1', p: /(Windows 8.1|Windows NT 6.3)/ },
      { v: '8.0', p: /(Windows 8|Windows NT 6.2)/ },
      { v: '10.0', p: /(Windows 10|Windows NT 10.0)/ },
      { v: '3.0', p: /Windows CE/ },
      { v: '3.1', p: /Win16/ },
      { v: '3.2', p: /(Windows 95|Win95|Windows_95)/ },
      { v: '3.5', p: /(Win 9x 4.90|Windows ME)/ },
      { v: '3.6', p: /(Windows 98|Win98)/ },
      { v: '3.7', p: /Windows ME/ },
      { v: '4.0', p: /(Windows NT 4.0|WinNT4.0|WinNT|Windows NT)/ },
      { v: '4.0', p: /(Windows NT 5.0|Windows 2000)/ },
    ];

    for (let i = 0; i < css.length; i++) {
      const cs = css[i];
      try {
        if (cs.p.test(ua)) {
          version = cs.v;
          break;
        }
      } catch (e) {
        //
      }
    }
  } else if (npDefine.mac) {
    const match = /Mac OS X ([0-9.]*)[._]([0-9.]*)/.exec(ua);
    if (match) {
      version = match[1] + '.' + match[2];
    }
  } else if (npDefine.lnx) {
    //
  }
  // npConsole.log("운영체제 버전 : [" + version + "]");
  return version;
})();

npDefine.bwVersion = (function () {
  let version;
  let temp;
  const ua = nua;
  if (npDefine.ff) {
    version = ua.substring(ua.toLowerCase().lastIndexOf('firefox'));
    if (version.includes(' ')) {
      version = version.substring(0, version.indexOf(' '));
    }
    temp = version.split('/');
    return temp[1];
  } else if (npDefine.op) {
    if (ua.includes('OPR/')) {
      version = ua.split('OPR/')[1];
    } else if (ua.includes('Opera')) {
      version = ua.split('Opera/')[1];
    }

    if (version.includes(' ')) {
      temp = version.split(' ');
      return temp[0];
    } else {
      return version;
    }
  } else if (npDefine.cr || npDefine.b360) {
    version = ua.substring(ua.toLowerCase().lastIndexOf('chrome'));
    if (version.includes(' ')) {
      version = version.substring(0, version.indexOf(' '));
      temp = version.split('/');
      return temp[1];
    }
  } else if (npDefine.sf) {
    const regexp = new RegExp(/Version[\\/\s](\d+\.\d+)/.test(nua));
    const bwVer = regexp.$1;
    return bwVer;
  } else if (npDefine.ie || npDefine.qq) {
    if (ua.includes('MSIE')) {
      let tua = '';
      tua = ua.substring(ua.indexOf('MSIE') + 4, ua.length);
      tua = tua.replace(/(^\s*)|(\s*$)/gi, '');
      temp = tua.split(';');
      temp = temp[0].split(' ');
      return temp[0];
    } else {
      // IE11
      return ua.substring(ua.indexOf('rv:') + 3, ua.indexOf('rv:') + 7);
    }
  } else if (npDefine.edge) {
    const idx1 = ua.lastIndexOf('Edge/');
    const idx2 = ua.lastIndexOf('Edg/');
    const idx = idx1 >= 0 ? idx1 : idx2;
    version = ua.substring(idx);
    if (version.includes(' ')) {
      version = version.substring(0, version.indexOf(' '));
      temp = version.split('/');
      return temp[1];
    } else {
      temp = version.split('/');
      return temp[1];
    }
  }
  // npConsole.log("브라우저 버전 : [" + version + "]");
})();

export const npCommon = class npCommon {
  static isNull(data) {
    if (typeof data === 'undefined' || data == null) {
      return true;
    }
    return false;
  }

  static isBlank(data) {
    if (typeof data === 'undefined' || data == null || data === '') {
      return true;
    }
    return false;
  }

  static n2b(data, def) {
    if (this.isNull(def)) def = '';
    if (this.isNull(data)) return def;
    return data;
  }

  static trim(str) {
    if (str == null) return str;
    try {
      return str.replace(/(^\s*)|(\s*$)/gi, '');
    } catch (e) {
      try {
        return str.replace(/^\s+|\s+$/g, '');
      } catch (e) {
        //
      }
    }
    return str;
  }

  static hexEncode(byteArray) {
    let result = '';
    if (!byteArray) {
      return;
    }
    for (let i = 0; i < byteArray.length; i++) {
      result += (byteArray[i] < 16 ? '0' : '') + byteArray[i].toString(16);
    }
    return result;
  }

  static hexDecode(hex, charset) {
    if (npCommon.isBlank(hex)) {
      return '';
    }

    let r = '';
    if (hex.indexOf('0x') === 0 || hex.indexOf('0X') === 0) {
      hex = hex.substr(2);
    }
    if (hex.length % 2) {
      hex += '0';
    }
    const arr = [];
    for (let i = 0; i < hex.length; i += 2) {
      arr.push(parseInt(hex.slice(i, i + 2), 16));
    }
    if (charset === 'UTF8') {
      return toString(arr);
    } else {
      for (let i = 0; i < arr.length; i++) {
        r += String.fromCharCode(arr[i]);
      }
    }
    return r;
  }

  static getRandomBytes(howMany) {
    let i;
    const bytes = [];
    for (i = 0; i < howMany; i++) {
      bytes[i] = Math.round(Math.random() * 255);
    }
    return bytes;
  }

  static getBytes(plaintext) {
    const bytes = [];
    if (typeof plaintext === 'string' || plaintext.indexOf) {
      /*
			bytes = plaintext.split("");
			for (i=0; i < bytes.length; i++) {
				bytes[i] = bytes[i].charCodeAt(0) & 0xff;
			}
			*/
      const utf8 = [];
      const str = plaintext;
      for (let i = 0; i < str.length; i++) {
        let charcode = str.charCodeAt(i);
        if (charcode < 0x80) utf8.push(charcode);
        else if (charcode < 0x800) {
          utf8.push(0xc0 | (charcode >> 6), 0x80 | (charcode & 0x3f));
        } else if (charcode < 0xd800 || charcode >= 0xe000) {
          utf8.push(
            0xe0 | (charcode >> 12),
            0x80 | ((charcode >> 6) & 0x3f),
            0x80 | (charcode & 0x3f),
          );
        } else {
          i++;
          // UTF-16 encodes 0x10000-0x10FFFF by
          // subtracting 0x10000 and splitting the
          // 20 bits of 0x0-0xFFFFF into two halves
          charcode =
            0x10000 +
            (((charcode & 0x3ff) << 10) | (str.charCodeAt(i) & 0x3ff));
          utf8.push(
            0xf0 | (charcode >> 18),
            0x80 | ((charcode >> 12) & 0x3f),
            0x80 | ((charcode >> 6) & 0x3f),
            0x80 | (charcode & 0x3f),
          );
        }
      }
      return utf8;
    }
    return bytes;
  }

  static makeUuid() {
    let dummy = Math.floor(Math.random() * 99) + 1;
    // dummy 값이 두자리가 아닐 경우 패딩 값이 맨 앞자리에 생성됨
    if (dummy < 10) dummy = dummy + 10;
    let uuid = new Date().getTime().toString() + dummy;
    for (let len = uuid.length; len < 15; len++) {
      uuid = '0' + uuid;
    }
    return uuid;
  }

  static splitVersion(strData) {
    // var arr = strData.split(/ |,|\.|\_|\//g);
    const arr = strData.split(/ |,|\.|_|\//g);
    const ret = [];
    let arrIndex = 0;
    for (let index = 0; index < arr.length; index++) {
      if (!npCommon.isBlank(arr[index])) {
        ret[arrIndex] = arr[index];
        arrIndex++;
      }
    }
    if (ret.length > 0) return ret;

    return null;
  }

  static checkVersion(ver, min, max) {
    let result = true;
    if (!npCommon.isBlank(min)) {
      result = result && npCommon.compareVersion(ver, min);
    }
    if (!npCommon.isBlank(max)) {
      result = result && npCommon.compareVersion(max, ver);
    }
    return result;
  }

  static compareVersion(strOrgVer, strVer, strSplitData) {
    if (npCommon.isBlank(strOrgVer)) return false;
    if (npCommon.isBlank(strVer)) return false;
    // if(this.isBlank(strSplitData)) return false;

    const orgVerArr = this.splitVersion('' + strOrgVer);
    const orgVerArrLen = orgVerArr.length;
    const verArr = this.splitVersion('' + strVer);

    let i = 0;
    for (i = 0; i < orgVerArrLen; i++) {
      if (npCommon.isNull(verArr[i])) verArr[i] = 0;
      orgVerArr[i] = parseInt(orgVerArr[i], 10);
      verArr[i] = parseInt(verArr[i], 10);

      if (orgVerArr[i] > verArr[i]) {
        return true;
      } else if (orgVerArr[i] < verArr[i]) {
        return false;
      }
    }
    if (i === orgVerArrLen) {
      return true;
    }

    if (orgVerArr.toString() === verArr.toString()) {
      return true;
    }
    return false;
  }

  static eraseSpecialChars(tmp) {
    const regExp = /[{}[]\/?.,;:|\)*~`!^-+<>@#$%&\\=\('"]/gi;
    if (regExp.test(tmp)) {
      const t = tmp.replace(regExp, '');
      tmp = t;
    }
    return tmp;
  }

  static comma(str) {
    str = String(str);
    return str.replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,');
  }

  static uncomma(str) {
    str = String(str);
    return str.replace(/[^\d]+/g, '');
  }

  static arrayIn(arr, value) {
    for (let i = 0; i < arr.length; i++) {
      if (arr[i] === value) {
        return true;
      }
    }
    return false;
  }

  static indexOf(arr, value) {
    for (let i = 0; i < arr.length; i++) {
      if (arr[i] === value) {
        return i;
      }
    }
    return -1;
  }

  static arrayNotIn(arr, value) {
    for (let i = 0; i < arr.length; i++) {
      if (arr[i] === value) {
        return false;
      }
    }
    return true;
  }

  static stopEvent(event) {
    event.preventDefault ? event.preventDefault() : (event.returnValue = false);
  }

  static formatPlaintext(plaintext) {
    const bpb = 128 / 8; // bytes per block
    let i;

    // if primitive string or String instance
    if (typeof plaintext === 'string' || plaintext.indexOf) {
      plaintext = plaintext.split('');
      // Unicode issues here (ignoring high byte)
      for (i = 0; i < plaintext.length; i++) {
        plaintext[i] = plaintext[i].charCodeAt(0) & 0xff;
      }
    }

    // zero padding
    for (i = bpb - (plaintext.length % bpb); i > 0 && i < bpb; i--) {
      plaintext[plaintext.length] = 0;
    }
    return plaintext;
  }

  static makeLength(text) {
    class Padder {
      constructor(len, pad) {
        if (len === undefined) {
          len = 1;
        } else if (pad === undefined) {
          pad = '0';
        }

        this.pads = '';
        while (this.pads.length < len) {
          this.pads += pad;
        }
      }

      pad(what) {
        const s = what.toString();
        return this.pads.substring(0, this.pads.length - s.length) + s;
      }
    }

    if (this.isNull(text)) {
      text = '';
    }
    const length = text.length;
    const datalength = new Padder(4);
    return datalength.pad(length.toString(16));

    // if (this.isNull(text)) {
    // 	text = "";
    // }
    // let ret = (text.length).toString(16);
    // if(ret.length <= 4) {
    // 	while(ret.length < 4) {
    // 		ret = '0' + ret;
    // 	}
    // }
    // console.log("................", ret);
    // return ret;
  }

  static setCookie(name, value, expires, path, domain) {
    try {
      let cookie = name + '=' + escape(value) + ';';
      if (expires) {
        if (expires instanceof Date) {
          if (isNaN(expires.getTime())) {
            expires = new Date();
          }
        } else {
          expires = new Date(
            new Date().getTime() + parseInt(expires, 10) * 1000 * 60 * 60 * 24,
          );
        }
        cookie += 'expires=' + expires.toGMTString() + ';';
      }
      if (!npCommon.isBlank(path)) cookie += 'path=' + path + ';';
      if (!npCommon.isBlank(domain)) cookie += 'domain=' + domain + ';';
      document.cookie = cookie;
    } catch (e) {
      // do nothing
    }
  }

  static getCookie(name) {
    name = name + '=';
    const cookieData = document.cookie;
    let start = cookieData.indexOf(name);
    let value = '';
    if (start !== -1) {
      start += name.length;
      let end = cookieData.indexOf(';', start);
      if (end === -1) end = cookieData.length;
      value = cookieData.substring(start, end);
    }
    return unescape(value);
  }

  static encode(text) {
    return encodeURIComponent(text);
  }

  static encrypt(plaintext, key, mode) {
    const blockSizeInBits = 128;
    // const keySizeInBits = 256;

    const bpb = blockSizeInBits / 8; // bytes per block
    let i, aBlock;
    let ct; // ciphertext

    // console.log("plaintext 111 : ", key.length * 8);

    if (!plaintext || !key) return;
    // if (key.length * 8 != keySizeInBits) return;

    if (mode === 'CBC') ct = npCommon.getRandomBytes(bpb);
    // get IV
    else {
      mode = 'ECB';
      ct = [];
    }

    // convert plaintext to byte array and pad with zeros if necessary.
    plaintext = npCommon.formatPlaintext(plaintext);

    // console.log("plaintext : " + npCommon.hexEncode(plaintext));

    const expandedKey = new npAES.keyExpansion(key);
    for (let block = 0; block < plaintext.length / bpb; block++) {
      aBlock = plaintext.slice(block * bpb, (block + 1) * bpb);
      if (mode === 'CBC') {
        for (i = 0; i < bpb; i++) {
          aBlock[i] ^= ct[block * bpb + i];
        }
      }
      ct = ct.concat(npAES.AESencrypt(aBlock, expandedKey));
    }

    return npCommon.hexEncode(ct);
  }

  static decrypt(ciphertext, key, mode, charset) {
    const blockSizeInBits = 128;
    // const keySizeInBits = 256;

    const bpb = blockSizeInBits / 8; // bytes per block
    let pt = []; // plaintext array
    let aBlock; // a decrypted block
    let block; // current block number
    if (!ciphertext || !key) return;
    if (typeof ciphertext === 'string') {
      ciphertext = ciphertext.split('');
      for (let i = 0; i < ciphertext.length; i++) {
        ciphertext[i] = ciphertext[i].charCodeAt(0) & 0xff;
      }
    }

    // if (key.length * 8 != keySizeInBits) {
    // 	return;
    // }

    if (!mode) {
      mode = 'ECB'; // assume ECB if mode omitted
    }
    const expandedKey = new npAES.prepareDecryption(key);
    for (block = ciphertext.length / bpb - 1; block > 0; block--) {
      aBlock = npAES.AESdecrypt(
        ciphertext.slice(block * bpb, (block + 1) * bpb),
        expandedKey,
      );
      if (mode === 'CBC') {
        for (let i = 0; i < bpb; i++) {
          pt[(block - 1) * bpb + i] =
            aBlock[i] ^ ciphertext[(block - 1) * bpb + i];
        }
      } else {
        pt = aBlock.concat(pt);
      }
    }
    // do last block if ECB (skips the IV in CBC)
    if (mode === 'ECB') {
      pt = npAES.AESdecrypt(ciphertext.slice(0, bpb), expandedKey).concat(pt);
    }

    // cleanup zero padding
    let tval = pt[pt.length - 1];
    while (typeof tval === 'undefined' || tval == null || tval === 0) {
      pt.pop();
      tval = pt[pt.length - 1];
    }

    // return String.fromCharCode.apply(null, new Uint8Array(npCommon.hexDecode(npCommon.hexEncode(pt))));
    return npCommon.hexDecode(npCommon.hexEncode(pt), charset);
  }

  static parsePublicKey(result) {
    const ret = {};
    try {
      ret.seedkey = npCommon.hexEncode(npCommon.getRandomBytes(32));
      let ppm = null;
      result = npCommon.trim(result);
      if (!npCommon.isBlank(result) && result.length > 64) {
        const key = result.substring(0, 64);
        const data = result.substring(64);
        ppm = npCommon.decrypt(
          npCommon.hexDecode(data),
          npCommon.hexDecode(key),
          'ECB',
        );
      }

      if (npCommon.isBlank(ppm)) {
        alert(npMessage.m30);
        ret.rsa = false;
        return ret;
      }

      const rsa = npRSA.parse(ppm);
      const enc = rsa.encrypt(ret.seedkey);

      ret.rsakey = enc;
      ret.rsa = true;
    } catch (e) {
      ret.rsa = false;
      ret.message = e.message;
    }
    return ret;
  }

  static get(params) {
    return this.ajax('get', npConfig.APPLICATION_URL, params);
  }

  static post(params) {
    return this.ajax('post', npConfig.APPLICATION_URL, params);
  }

  static getElapsedTime(startTime) {
    let ret = -1;
    try {
      const endTime = new Date();
      ret = endTime - startTime;
      if (ret > 100000) ret = 99999;
    } catch (e) {
      //
    }
    return ret + '';
  }

  static ajax(method, url, params) {
    return new Promise(function (resolve, reject) {
      // this.vue.$http.defaults.headers.common['Origin'] = "http://192.168.1.85:9999";
      // this.vue.$http.defaults.headers.common['Content-Type'] = "text/plain";
      // delete this.vue.$http.defaults.headers.common['Authorization'];
      // delete this.vue.$http.defaults.headers.common['AuthorizationKey'];
      // axios({

      let data = params;
      if (method === 'post') {
        // let formData = new FormData();
        // for(const _key in params) {
        //	 const _value = params[_key];
        //	 formData.set(_key, _value);
        // }
        // data = formData;
        // console.log("formData", formData);

        if (typeof params !== 'string') {
          const querystring = require('querystring');
          data = querystring.stringify(params);
        }
      }
      // this.vue.$http({
      axios({
        method,
        url,
        params: method !== 'post' ? data : null,
        data: method === 'post' ? data : null,
        headers: {
          // 'Authorization': null,
          // 'AuthorizationKey': null,
          // 'Access-Control-Request-Headers': null
        },
        withCredentials: true,
      })
        // this.vue.$http.post(url, formData)
        // this.vue.$http.post(url, params)
        .then(
          function (response) {
            // console.log(response);
            if (typeof response === 'string') {
              resolve(response);
              return;
            }
            if (response.status === 200 && response.data) {
              // console.log(response.data);

              // const data = response.data;
              // const { code, message } = data;
              // if(code === "1000") {
              //	 _this.saveToken(data, _callback);
              // } else if(code === "2001" || code === "2002") {
              //	 _callback(true, code, "", data);
              // } else {
              //	 _callback(false, code, message);
              // }
              resolve(response.data);
            } else {
              resolve(response);
            }
          },
          function (err) {
            // console.log('err', err)
            // _callback(false, "", err);
            reject(err);
          },
        );
    });
  }

  static dispatchEvent(element, type, params) {
    let event;
    if (typeof Event === 'function') {
      event = new Event(type, { bubbles: true });
    } else {
      event = document.createEvent('Event');
      event.initEvent(type, true, true);
    }
    if (params) {
      for (const key in params) {
        event[key] = params[key];
      }
    }
    element.dispatchEvent(event);
  }
};

