export const npCommand = class npCommand {
  constructor(options) {
    this.options = Object.assign(
      {
        protocol: 'https',
        host: '127.0.0.1',
        port: 14440,
        range: 10,
      },
      options || {},
    );

    this.protocol = this.options.protocol;
    this.port = this.options.port;
    this.url =
      this.options.protocol +
      '://' +
      this.options.host +
      ':' +
      this.options.port;
    this.useWebSocket = this.options.protocol === 'wss';
    // console.log(this.url);
  }

  static makeHeader(pcode, sync, headers) {
    if (npCommon.isNull(headers) || !Array.isArray(headers)) {
      const random = npCommon.getRandomBytes(16);
      headers = new Array(4);
      headers[0] = '';
      //			headers[0] = document.domain;
      headers[1] = npConfig.CUSTOMER_ID;
      headers[2] =
        npCommon.hexEncode(random) +
        npCommon.encrypt(
          document.domain,
          npCommon.hexDecode(npCommon.hexEncode(random)),
          'ECB',
          128,
        );
      headers[3] = '1000';

      //			headers = new Array(2);
      //			headers[0] = document.domain;
      //			headers[1] = npPacket.CUSTOMER_ID;
    }

    const headerCount = headers.length;

    const command = [];
    command.push(pcode);
    if (npCommon.isBlank(sync)) {
      command.push('1');
    } else {
      command.push(sync);
    }

    command.push(headerCount);
    for (let idx = 0; idx < headerCount; idx++) {
      command.push(npCommon.makeLength(headers[idx]));
      command.push(headers[idx]);
    }

    // console.log("command header", command);

    return command;
  }

  static makeCommand(pcode, sync, ccode, parameter) {
    const command = npCommand.makeHeader(pcode, sync);

    if (!npCommon.isBlank(ccode)) {
      command.push(ccode);
    }

    if (!npCommon.isBlank(parameter)) {
      command.push(npCommon.makeLength(parameter));
      command.push(parameter);
    }

    return command.join('');
  }

  doCommand(command) {
    if (this.useWebSocket) {
      return this.doWebSocket(command);
    }

    // axios.defaults.baseURL = 'http://192.168.1.85:8080';
    // axios.defaults.headers.post['Content-Type'] = 'application/json;charset=utf-8';
    // axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';

    const _self = this;

    return new Promise(function (resolve, reject) {
      let data = command;
      if (typeof command !== 'string') {
        const querystring = require('querystring');
        data = querystring.stringify(command);
      }

      if (npConfig.DEBUG_MODE) console.log('REQUEST', data);

      axios({
        method: 'post',
        url: _self.url,
        // params : (method !== "post" ? data : null),
        // data: (method === "post" ? data : null),
        data,
        headers: {
          // 'Authorization': null,
          // 'AuthorizationKey': null,
          // 'Access-Control-Request-Headers': null
          // 'Access-Control-Allow-Origin': "*"
        },
        // withCredentials: true
      }).then(
        function (response) {
          // console.log(response);
          if (typeof response === 'string') {
            if (npConfig.DEBUG_MODE) console.log('RESPONSE', response);
            resolve({ instance: _self, data: response });
            return;
          }

          if (response.status === 200 && response.data) {
            if (npConfig.DEBUG_MODE) console.log('RESPONSE', response.data);
            resolve({ instance: _self, data: response.data });
          } else {
            resolve({ instance: _self, data: response });
          }
        },
        function (err) {
          // console.log('err', _self.url, err)
          reject(err);
        },
      );
    });

    // return new Promise(function(resolve, reject) {
    // 	console.log("REQUEST", command);

    // 	npCommon.ajax("post", _self.url, command)
    // 	.then(function(data) {
    // 		resolve(data);
    // 	})
    // 	.catch((err) => {
    // 		console.log(err);
    // 		reject(err);
    // 	});
    // });
  }

  doWebSocket(command) {
    const _self = this;

    return new Promise(function (resolve, reject) {
      let data = command;
      if (typeof command !== 'string') {
        const querystring = require('querystring');
        data = querystring.stringify(command);
      }

      if (npConfig.DEBUG_MODE) console.log('REQUEST', data);

      // if(o.timeout > 0) {
      // 	timeoutid = setTimeout(function(){
      // 		//console.log(o);
      // 		if(timeoutid != null) {
      // 			docallback({readyState : 4, status : 999, responseText : ""});
      // 		}
      // 	}, o.timeout);
      // }

      const socket = new window.WebSocket(_self.url);
      socket.onopen = function (event) {
        // socket.send("Here's some text that the server is urgently awaiting!");
        // console.log("Websocket opened...");
        socket.send(command);
      };
      socket.onclose = function (event) {
        // console.log("Websocket closed...");
      };
      socket.onerror = function (event) {
        // console.log("Websocket error...");
        socket.close();
        // if(timeoutid != null) resetTimeout();
        // docallback({readyState : 4, status : 999, responseText : "WebSocket Error."});
        // resolve({instance : _self, data : event.data});
        reject(event);
      };
      socket.onmessage = function (event) {
        // docallback({readyState : 4, status : 200, responseText : event.data});
        resolve({ instance: _self, data: event.data });
        socket.close();
      };
    });
  }

  handshake() {
    if (this.options.port < 0)
      return Promise.reject(
        new Error('NOS 통신을 위한 연결포트가 정의되지 않았습니다.'),
      );

    // 핸드쉐이크 명령 생성
    const paramCount = '1'; // 파라메터개수
    const random = npCommon.getRandomBytes(16);
    const hp =
      npCommon.hexEncode(random) +
      npCommon.encrypt(
        nua,
        npCommon.hexDecode(npCommon.hexEncode(random)),
        'ECB',
        128,
      );
    const body = paramCount + npCommon.makeLength(hp) + hp;
    const command = npCommand.makeCommand(
      npPacket.PRODUCT_DM,
      npPacket.MODE_SYNC,
      npPacket.CMD_HANDSHAKE,
      body,
    );

    return this.doCommand(command);
  }
};

export const npHangul = {
  // static Hangul = {
  initial: [
    12593, 12594, 12596, 12599, 12600, 12601, 12609, 12610, 12611, 12613, 12614,
    12615, 12616, 12617, 12618, 12619, 12620, 12621, 12622,
  ],
  finale: [
    0, 12593, 12594, 12595, 12596, 12597, 12598, 12599, 12601, 12602, 12603,
    12604, 12605, 12606, 12607, 12608, 12609, 12610, 12612, 12613, 12614, 12615,
    12616, 12618, 12619, 12620, 12621, 12622,
  ],
  dMedial: [
    0, 0, 0, 0, 0, 0, 0, 0, 0, 800, 801, 820, 0, 0, 1304, 1305, 1320, 0, 0,
    1820,
  ],
  dFinale: [
    0, 0, 0, 119, 0, 422, 427, 0, 0, 801, 816, 817, 819, 825, 826, 827, 0, 0,
    1719, 0, 1919,
  ],
  SBase: 44032,
  VCount: 21,
  LCount: 19,
  TCount: 28,
  NCount: 588,
  VBase: 12623,
  SCount: 11172,
  composeHangul(word) {
    // 한글 조합
    const wordLength = word.length;
    const firstKeyCode = word.charCodeAt(0);
    let returnValue = word; // 1자일 경우
    let combineKeyCode = 0;
    for (let i = 1; i < wordLength; i++) {
      const prevKeyCode = word.charCodeAt(i - 1);
      let curKeyCode = word.charCodeAt(i);
      const prevWord = String.fromCharCode(prevKeyCode);
      let idxLastKey = '';
      let subCurKey = '';
      let subPrevKey = '';
      const idxFirstKey = this.findCode(this.initial, prevKeyCode);
      if (idxFirstKey !== -1) {
        // 이전 단어가 초성일 때
        subCurKey = curKeyCode - this.VBase;
        if (subCurKey >= 0 && subCurKey < this.VCount) {
          // 현재 단어가 중성일 때
          combineKeyCode =
            this.SBase + (idxFirstKey * this.VCount + subCurKey) * this.TCount;
          returnValue =
            prevWord.slice(0, prevWord.length - 1) +
            String.fromCharCode(combineKeyCode);
          continue;
        }
      }
      subPrevKey = prevKeyCode - this.SBase;
      if (
        subPrevKey >= 0 &&
        subPrevKey < 11145 &&
        subPrevKey % this.TCount === 0
      ) {
        // 이전 단어가 '히' 전이면  11145 = D789 = '힉';
        idxLastKey = this.findCode(this.finale, curKeyCode);
        if (idxLastKey !== -1) {
          combineKeyCode = prevKeyCode + idxLastKey;
          returnValue =
            prevWord.slice(0, prevWord.length - 1) +
            String.fromCharCode(combineKeyCode);
          continue;
        }
        subCurKey = (subPrevKey % this.NCount) / this.TCount;
        const tmp = this.findCode(
          this.dMedial,
          subCurKey * 100 + (curKeyCode - this.VBase),
        );
        if (tmp > 0) {
          combineKeyCode = prevKeyCode + (tmp - subCurKey) * this.TCount;
          returnValue =
            prevWord.slice(0, prevWord.length - 1) +
            String.fromCharCode(combineKeyCode);
        }
      }
      if (
        subPrevKey >= 0 &&
        subPrevKey < 11172 &&
        subPrevKey % this.TCount !== 0
      ) {
        // 이전 단어가 한글의 끝 이면 11172 = D7A4 = ' 힤'
        idxLastKey = subPrevKey % this.TCount;
        subCurKey = curKeyCode - this.VBase;
        if (subCurKey >= 0 && subCurKey < this.VCount) {
          // 현재 단어가 중성일 때
          curKeyCode = this.findCode(this.initial, this.finale[idxLastKey]);
          if (curKeyCode >= 0 && curKeyCode < this.LCount) {
            const tmpPrevWord =
              prevWord.slice(0, prevWord.length - 1) +
              String.fromCharCode(firstKeyCode - idxLastKey);
            const tmpFirstKeyCode =
              this.SBase + (curKeyCode * this.VCount + subCurKey) * this.TCount;
            returnValue = tmpPrevWord + String.fromCharCode(tmpFirstKeyCode);
            continue;
          }
          if (
            idxLastKey < this.dFinale.length &&
            this.dFinale[idxLastKey] !== 0
          ) {
            const tmpPrevWord =
              prevWord.slice(0, prevWord.length - 1) +
              String.fromCharCode(
                firstKeyCode -
                  idxLastKey +
                  Math.floor(this.dFinale[idxLastKey] / 100),
              ); // 받침빼기
            const tmp = this.findCode(
              this.initial,
              this.finale[this.dFinale[idxLastKey] % 100],
            );
            const tmpFirstKeyCode =
              this.SBase + (tmp * this.VCount + subCurKey) * this.TCount;
            returnValue = tmpPrevWord + String.fromCharCode(tmpFirstKeyCode);
          }
        }
        const tmpIdx = this.findCode(this.finale, curKeyCode);
        const c = this.findCode(this.dFinale, idxLastKey * 100 + tmpIdx);
        if (c > 0) {
          combineKeyCode = prevKeyCode + c - idxLastKey;
          returnValue =
            prevWord.slice(0, prevWord.length - 1) +
            String.fromCharCode(combineKeyCode);
          continue;
        }
      }
    }

    // for end
    return returnValue;
  }, // end combineHangul
  findCode(codeList, key) {
    for (let i = 0; i < codeList.length; i++) {
      if (codeList[i] === key) {
        return i;
      }
    }
    return -1;
  }, // end findCode
  backSpace(word) {
    const wordLength = word.length;
    let returnValue = '';
    let idxFirstKey, idxMiddleKey, idxLastKey;
    for (let i = 0; i < wordLength; i++) {
      const curKeyCode = word.charCodeAt(i);
      const idxCurKey = curKeyCode - this.SBase;
      if (idxCurKey < 0 || idxCurKey >= this.SCount) {
        returnValue = String.fromCharCode(curKeyCode);
        continue;
      }
      idxFirstKey = this.initial[Math.floor(idxCurKey / this.NCount)];
      idxMiddleKey = this.VBase + (idxCurKey % this.NCount) / this.TCount;
      idxLastKey = this.finale[idxCurKey % this.TCount];
      returnValue = String.fromCharCode(idxFirstKey, idxMiddleKey);
      if (idxLastKey !== 0) {
        returnValue = returnValue + String.fromCharCode(idxLastKey);
      }
    }
    return returnValue;
  },
  splitWord(word, idxSplit) {
    const staticWord = word.substring(0, word.length - idxSplit);
    const tmpWord = word.substring(word.length - idxSplit, word.length);
    // const returnArr = new Array(staticWord, tmpWord);
    const returnArr = [];
    returnArr.push(staticWord);
    returnArr.push(tmpWord);
    return returnArr;
  },
};

