const cdstoragepath =
  'https://supdate.nprotect.net/nprotect/nos_service/nppfs.cds.html';
export const npCDS = class npCDS {
  constructor(options) {
    this.options = Object.assign({}, options || {});

    // const e = cdstoragepath.match(/^(https?\:)\/\/(([^:\/?#]*)(?:\:([0-9]+))?)/);
    const e = cdstoragepath.match(/^(https?:)\/\/(([^:/?#]*)(?::([0-9]+))?)/);
    const _self = this;
    _self.id = 0;
    _self.iframe = null;
    _self.iframeReady = false;
    _self.origin =
      typeof e[4] === 'undefined' || e[4] === ''
        ? e[1] + '//' + e[3]
        : e[1] + '//' + e[3] + ':' + e[4];
    _self.path = cdstoragepath;
    _self.queue = [];
    _self.requests = {};
    _self.eventPrefix = window.addEventListener ? '' : 'on';
    _self.eventAddFunc = window.addEventListener
      ? 'addEventListener'
      : 'attachEvent';
    // console.log(this.url);
  }

  init() {
    // npConsole.log("npCDS init");
    const _self = this;
    // var _iframe = _self.iframe || npQuery("#nppfsiframe").get(0);
    let _iframe = _self.iframe || document.getElementById('nppfsiframe');
    if (!_iframe) {
      _iframe = document.createElement('iframe');
      _iframe.src = _self.path;
      _iframe.onload = _self.iframeLoaded.bind(_self);
      _iframe.id = 'npfdsiframe';
      _iframe.style.display = 'none';
      _self.iframe = _iframe;
      document.body.append(_iframe);
    }
    window[_self.eventAddFunc](
      _self.eventPrefix + 'message',
      _self.handleMessage.bind(_self),
      true,
    );
  }

  requestValue(key, value, callback) {
    const _self = this;
    const params = {
      callback,
      request: {
        id: ++_self.id,
        key,
        value,
      },
    };
    if (_self.iframeReady) {
      _self.sendRequest(params);
    } else {
      _self.queue.push(params);
      if (!_self.iframe) {
        _self.init();
      }
    }
  }

  sendRequest(params) {
    const _self = this;
    _self.iframe.contentWindow.postMessage(
      JSON.stringify(params.request),
      _self.origin,
    );
    _self.requests[params.request.id] = params;
  }

  iframeLoaded() {
    const _self = this;
    const t = _self.queue;
    let r = t.length;
    for (_self.iframeReady = true; r--; ) {
      _self.sendRequest(t.pop());
    }
  }

  handleMessage(e) {
    if (e.origin === this.origin) {
      const tryCatch = function (func) {
        try {
          return func();
        } catch (t) {
          console.log(t);
        }
      };
      const n = this;
      tryCatch(function () {
        // var a = npQuery.parseJSON(e.data);
        const a = JSON.parse(e.data);
        const o = n.requests[a.id];
        if (o) {
          o.callback && o.callback(a.value.deviceId);
          delete n.requests[a.id];
          //					delete window.npStorage.cds;
        }
      });
    }
  }
};

export const npStorage = class npStorage {
  constructor(options) {
    this.options = Object.assign({}, options || {});

    this.cds = null;
    this.storageValues = {};
  }

  static storageKey = 'npCloudFdsDevice';
  static storedKey = 'npCloudFdsDeviceInfo';

  crossDomainStorage(key, value, callback) {
    let storage = this.cds;
    if (!storage) {
      this.cds = storage = new npCDS();
    }
    storage.requestValue(key, value, callback);
  }

  read(storageNode, key) {
    const _self = this;

    // npConsole.log(storageNode + " => " + key);
    return new Promise(function (resolve, reject) {
      //   const timeoutid = setTimeout(function () {
      setTimeout(function () {
        resolve();
      }, 2 * 1000);

      const win = window;
      try {
        if (storageNode === 'crossDomainStorage') {
          _self.crossDomainStorage(key, null, function (res) {
            // npConsole.log(storageNode + " => " + value);
            // if(!!value) storageValues[storageNode] = npQuery.parseJSON(value);
            if (res) _self.storageValues[storageNode] = JSON.parse(res);
            resolve();
          });
        } else if (storageNode === 'windowName') {
          const value = !npCommon.isBlank(win.name) ? win.name : '';
          // npConsole.log(storageNode + " => " + value);
          // storageValues[storageNode] = npQuery.parseJSON(value);
          _self.storageValues[storageNode] = JSON.parse(value);
          resolve();
        } else if (storageNode === 'cookie') {
          const value = npCommon.getCookie('__' + key);
          // if(!!value) storageValues[storageNode] = npQuery.parseJSON(value);
          if (value) _self.storageValues[storageNode] = JSON.parse(value);
          resolve();
        } else if (
          storageNode === 'localStorage' ||
          storageNode === 'sessionStorage'
        ) {
          let value = '';
          if (storageNode === 'localStorage') {
            if (!win.localStorage) {
              resolve();
              return;
            }
            value = win.localStorage.getItem(key);
          } else if (storageNode === 'sessionStorage') {
            if (!win.sessionStorage) {
              resolve();
              return;
            }
            value = win.sessionStorage.getItem(key);
          }

          // npConsole.log(storageNode + " => " + value);
          // if(!!value) storageValues[storageNode] = npQuery.parseJSON(value);
          if (value) _self.storageValues[storageNode] = JSON.parse(value);
          resolve();
        } else if (storageNode === 'indexedDB') {
          const request = win.indexedDB.open(npStorage.storageKey, 1);
          request.onupgradeneeded = function (e) {
            try {
              const db = e.target.result;
              db.createObjectStore(npStorage.storedKey, { keyPath: 'key' }).add(
                { key, value: '' },
              );
            } catch (t) {
              //
            }
          };
          request.onsuccess = function (event) {
            const db = event.target.result;
            if (db) {
              try {
                if (!db.objectStoreNames.contains(npStorage.storedKey)) {
                  db.close();
                  win.indexedDB.deleteDatabase(npStorage.storageKey);
                  resolve();
                  return;
                }
                db
                  .transaction([npStorage.storedKey], 'readonly')
                  .objectStore(npStorage.storedKey)
                  .get(key).onsuccess = function (event) {
                  if (
                    event &&
                    event.target &&
                    event.target.result &&
                    event.target.result.value
                  ) {
                    const value = event.target.result.value;
                    // npConsole.log(storageNode + " => " + value);
                    // if(!!value) storageValues[storageNode] = npQuery.parseJSON(value);
                    if (value)
                      _self.storageValues[storageNode] = JSON.parse(value);
                  }
                  resolve();
                };
              } catch (i) {
                resolve();
                // npConsole.log(i);
              }
            }
          };
          request.onerror = function (event) {
            win.indexedDB.deleteDatabase(npStorage.storageKey);
            resolve();
          };
        } else if (storageNode === 'webSQL') {
          const db = win.openDatabase(
            npStorage.storageKey,
            '1.0',
            'nProtect Online Security as a Service, Agentless Fds',
            1 * 1024 * 1024,
          );
          db.transaction(function (tx) {
            tx.executeSql(
              'select value from ' + npStorage.storedKey + ' where key = ?',
              [key],
              function (tx, results) {
                if (results.rows.length > 0) {
                  const result = results.rows.item(0);
                  // npConsole.log(storageNode + " => " + result.value);
                  // if(!!result.value) storageValues[storageNode] = npQuery.parseJSON(result.value);
                  if (result.value)
                    _self.storageValues[storageNode] = JSON.parse(result.value);
                }
                resolve();
              },
            );
          });
        } else if (storageNode === 'fileSystem') {
          const filesystem =
            win.requestFileSystem || win.webkitRequestFileSystem;
          filesystem(
            win.TEMPORARY,
            1024,
            function (fs) {
              fs.root.getFile('npCloudPfdsDevice.nfo', {}, function (t) {
                t.file(function (t) {
                  const reader = new FileReader();
                  reader.onloadend = function () {
                    // npConsole.log(storageNode + " => " + this.result);
                    // if(!!this.result) storageValues[storageNode] = npQuery.parseJSON(this.result);
                    if (this.result)
                      _self.storageValues[storageNode] = JSON.parse(
                        this.result,
                      );
                    resolve();
                  };
                  reader.readAsText(t);
                });
              });
            },
            function (e) {
              /*
						var msg = '';
						switch (e.code) {
							case FileError.QUOTA_EXCEEDED_ERR:
								msg = 'QUOTA_EXCEEDED_ERR';
								break;
							case FileError.NOT_FOUND_ERR:
								msg = 'NOT_FOUND_ERR';
								break;
							case FileError.SECURITY_ERR:
								msg = 'SECURITY_ERR';
								break;
							case FileError.INVALID_MODIFICATION_ERR:
								msg = 'INVALID_MODIFICATION_ERR';
								break;
							case FileError.INVALID_STATE_ERR:
								msg = 'INVALID_STATE_ERR';
								break;
							default:
								msg = 'Unknown Error';
								break;
						};
						npConsole.log('FileSystem Error: ' + msg);
*/
              resolve();
            },
          );
        }
      } catch (e) {
        console.log('Catch Block : ' + e.message);
        console.log(e);
        resolve();
      }
    });
  }

  load(key) {
    const _self = this;
    return new Promise(function (resolve, reject) {
      const promises = [];

      const storages = [
        'crossDomainStorage',
        'fileSystem',
        'indexedDB',
        'webSQL',
        'localStorage',
        'sessionStorage',
        'cookie',
      ];
      // var storages = ["crossDomainStorage", "indexedDB", "webSQL", "localStorage", "sessionStorage", "cookie"];
      for (let idx = 0; idx < storages.length; idx++) {
        promises.push(_self.read(storages[idx], key));
      }

      Promise.all(promises).then(function () {
        if (typeof values === 'object') {
          for (key in _self.storageValues) {
            const value = _self.storageValues[key];
            console.log(key + ' => ' + JSON.stringify(value));
          }
        }
        resolve(_self.storageValues);
      });
    });
  }

  store(key, value, callback) {
    const win = window;
    // const domain = document.domain;
    //		win.name = value;

    // this.setCookie = function(name, value, expires, path, domain) {
    try {
      npCommon.setCookie('__' + key, value, 356 * 5, '/');
    } catch (l) {
      /* */
    }
    try {
      localStorage.setItem(key, value);
    } catch (l) {
      /* */
    }
    try {
      sessionStorage.setItem(key, value);
    } catch (l) {
      /* */
    }
    try {
      npStorage.crossDomainStorage(key, value);
    } catch (l) {
      /* */
    }
    try {
      const rfs = win.requestFileSystem || win.webkitRequestFileSystem;

      rfs(win.TEMPORARY, 1024, function (entry) {
        // retrieve a file and truncate it
        entry.root.getFile(
          'npCloudPfdsDevice.nfo',
          { create: false },
          function (entry) {
            entry.remove(
              function () {},
              function (e) {},
            );
          },
          function (e) {},
        );
      });

      rfs(win.TEMPORARY, 1024, function (entry) {
        entry.root.getFile(
          'npCloudPfdsDevice.nfo',
          {
            create: true,
          },
          function (entry) {
            entry.createWriter(function (writer) {
              writer.onwriteend = function () {
                //							npConsole.log('Write Complete...');
                writer.onwriteend = null; // Avoid an infinite loop.
              };
              writer.onerror = function (e) {
                console.log('Write failed: ' + e.toString());
              };

              const blob = new Blob([value], { type: 'text/plain' });
              writer.write(blob);

              //	writer.seek(writer.length); // Start write position at EOF.
              //	writer.truncate(1);
              //	writer.truncate(0);
            });
          },
          function (e) {},
        );
      });
    } catch (l) {
      /* */
    }

    try {
      win.indexedDB =
        win.indexedDB ||
        win.mozIndexedDB ||
        win.webkitIndexedDB ||
        win.msIndexedDB;
      win.IDBTransaction =
        win.IDBTransaction || win.webkitIDBTransaction || win.msIDBTransaction;
      win.IDBKeyRange =
        win.IDBKeyRange || win.webkitIDBKeyRange || win.msIDBKeyRange;

      const request = win.indexedDB.open(npStorage.storageKey, 1);
      request.onupgradeneeded = function (e) {
        try {
          const db = e.target.result;
          db.createObjectStore(npStorage.storedKey, { keyPath: 'key' }).add({
            key,
            value,
          });
        } catch (t) {
          //
        }
      };
      request.onerror = function (e) {
        // alert("Database error: " + e.target.errorCode);
      };
      request.onsuccess = function (e) {
        try {
          const db = e.target.result;
          if (!db) return;

          if (!db.objectStoreNames.contains(npStorage.storedKey)) {
            db.close();
            win.indexedDB.deleteDatabase(npStorage.storageKey);
            return;
          }

          db.onerror = function () {
            console.log("Couldn't delete database");
          };

          const tx = db
            .transaction([npStorage.storedKey], 'readwrite')
            .objectStore(npStorage.storedKey);
          if (!tx) return;
          tx.delete(key).onsuccess = function () {
            tx.add({ key, value });
          };
        } catch (n) {
          /* */
        }
      };
    } catch (l) {
      /* */
    }

    try {
      const db = win.openDatabase(
        npStorage.storageKey,
        '1.0',
        'nProtect Online Security, Agentless Fds',
        1 * 1024 * 1024,
      );
      db.transaction(function (tx) {
        tx.executeSql(
          'create table if not exists ' + npStorage.storedKey + '(key, value)',
        );
      });

      db.transaction(function (tx) {
        tx.executeSql(
          'delete from ' + npStorage.storedKey + ' where key = ?',
          [key],
          function () {},
          function (tx, error) {
            // npConsole.log("Error : " + error.message);
          },
        );
        tx.executeSql(
          'insert into ' + npStorage.storedKey + ' (key, value) values (?, ?)',
          [key, value],
        );
      });
      db.transaction(function (tx) {});
    } catch (l) {
      // npConsole.log(l);
    }
  }

  remove(key, callback) {
    const win = window;
    //		win.name = "";

    try {
      npCommon.setCookie('__' + key, '');
    } catch (l) {
      console.log(l);
    }
    try {
      localStorage.removeItem(key);
    } catch (l) {
      console.log(l);
    }
    try {
      sessionStorage.removeItem(key);
    } catch (l) {
      console.log(l);
    }
    try {
      localStorage.removeItem('__npfdsCache');
    } catch (l) {
      console.log(l);
    }
    try {
      npStorage.crossDomainStorage();
    } catch (l) {
      console.log(l);
    }
    try {
      const rfs = win.requestFileSystem || win.webkitRequestFileSystem;
      rfs(win.TEMPORARY, 1024, function (e) {
        e.root.getFile(
          'npCloudPfdsDevice.nfo',
          {
            create: true,
          },
          function (e) {
            e.createWriter(function (e) {
              e.truncate(0);
            });
          },
        );
      });
    } catch (l) {
      //			npConsole.log(l);
    }
    try {
      const request = win.indexedDB.open(npStorage.storageKey, 1);
      request.onupgradeneeded = function (e) {
        try {
          const db = e.target.result;
          db.createObjectStore(npStorage.storedKey, { keyPath: 'key' }).add({
            key,
            value: '',
          });
        } catch (t) {
          //
        }
      };
      request.onsuccess = function (e) {
        try {
          e.target.result
            .transaction([npStorage.storedKey], 'readwrite')
            .objectStore(npStorage.storedKey)
            .delete(key);
        } catch (t) {
          //					npConsole.log(t);
        }
      };
    } catch (l) {
      console.log(l);
    }
    try {
      const db = win.openDatabase(
        npStorage.storageKey,
        '1.0',
        'nProtect Online Security as a Service, Agentless Fds',
        1 * 1024 * 1024,
      );
      db.transaction(function (tx) {
        tx.executeSql(
          'create table if not exists ' + npStorage.storedKey + '(key, value)',
        );
      });

      db.transaction(function (tx) {
        tx.executeSql('delete from ' + npStorage.storedKey + ' where key = ?', [
          key,
        ]);
      });
    } catch (l) {
      // npConsole.log(l);
    }
    callback && callback();
  }
};
