    'key-back-color': '#A9D0F5',
    're-text-color': '#FF0000',
    're-back-color': '#AFD7AF',
    'on-text-color': '#000000',
    'on-back-color': '#ffcc00',
    'off-text-color': '',
    'off-back-color': '',
