    'kpd-use-aria': true, // 시각장애인 지원
    'kpd-use-aria-mobile': true, // 모바일에서만 시각장애인 지원

    'kpd-preview-border': 2, // 미리보기 버튼 테두리 두께
    'kpd-preview-timeout': 2000, // 미리보기 노출 시간
    'kpd-close-hide': true, // 숨기기 버튼 클릭 시 마우스입력기 비활성화 하기
