<template>
  <div>
    <component :is="loading.component" ref="nppfs-loading"></component>
    
    <div class="nppfs-keypad-div" style="position: relative">
      <component
        :is="item.component"
        v-for="(item, name) in components"
        :key="item.id"
        :ref="item.ref"
        :name="name"
        v-bind="item.props"
        v-on="item.events"
      ></component>
    </div>
    <slot></slot>
  </div>
</template>

<script>
/* eslint-disable new-cap */
import npConfig from './nos.config.js';

export default {
  components: {},
  inject: {
    pvdKeypad: {
      default: () => {},
    },
  },
  props: {
    firewall: {
      type: [String, Boolean],
      required: false,
      default: false,
    },
    securekey: {
      type: [String, Boolean],
      required: false,
      default: false,
    },
    terminal: {
      type: [String, Boolean],
      required: false,
      default: false,
    },
    keypad: {
      type: [String, Boolean],
      required: false,
      default: false,
    },
    device: {
      type: [String, Boolean],
      required: false,
      default: false,
    },
    vendor: {
      type: [String],
      default: '',
    },
    showInstallPage: {
      type: [String, Boolean],
      required: false,
      default: false,
    },
  },
  data() {
    return {
      controller: '',
      components: {},
    };
  },

  computed: {
    loading() {
      return {
        component: npConfig.PAGES.LOADING,
        events: {
          loaded: (arg) => {},
        },
      };
    }
  },
  created(){

  },
  beforeDestroy() {
    this.controller.finalize();
  },
  mounted() {
    const npController = require('./npController.js');

    this.controller = new npController.default(this, {
      npn: this.firewall,
      npk: this.securekey,
      npf: this.terminal,
      kpd: this.keypad,
      npd: this.device,
      vendor: this.vendor,
    });
  },
  methods: {
    init() {
      this.start();
    },
    start() {
      const _this = this;

      _this.controller
        .init()
        .then(function () {
          _this.components = _this.controller.components();
        })
        .catch((err) => {
          
        });
    },
    destroy(id) {
      if (this.controller) this.controller.destroy(id);
    },

    table(id) {
      return this.controller.table(id);
    },
    replacement(id) {
      return this.controller.replacement(id);
    },
    encrypt(id) {
      return this.controller.encrypt(id);
    },

    toJSON() {
      return this.controller ? this.controller.toJSON() : {};
    }
  },
};
</script>
