<template>
  <!-- eslint-disable prettier/prettier  -->
  <div
    v-show="isVisible"
    style="position: fixed; z-index: 2000; top: 0px; left: 0px; height: 100%; width: 100%; opacity: 0.7; background: url(&quot;data:image/gif;base64,&quot;) 50% 50% no-repeat rgba(255, 255, 255, 0.7); display: none;"
  ></div>
</template>


<script>
export default {
  data() {
    return {
      isVisible: false,
    };
  },
  created() {},
  mounted() {},
  methods: {
    show() {
      this.isVisible = true;
    },
    hide() {
      this.isVisible = false;
    },
  }
};
</script>
