/* eslint-disable new-cap */
'use strict';
import { npCommand, npCommon, npDefine, npPacket, npMessage } from './npCommon';
import { npKeypad } from './npKeypad';
import npConfig from './nos.config.js';

const npController = class npController {
  constructor(_vue, options) {
    this.vue = _vue;

    this.options = Object.assign(
      {
        npn: false,
        npk: false,
        npf: false,
        kpd: false,
        npd: false,
        vendor: '',
      },
      options || {},
    );

    this.uuid = npCommon.makeUuid();
    this.keyset = {};

    this.initialized = false;
    this.absoluteUse = false;

    this.commandInstance = null;

    this.securekey = null;
    this.keypad = null;
    this.virtualMachine = false;

    this.modules = {};
  }

  showLoading() {
    const vue = this.vue;
    
    if (vue && vue.$refs && 'nppfs-loading' in vue.$refs)
      vue.$refs['nppfs-loading'].show();
  }

  hideLoading() {
    const vue = this.vue;

    if (vue && vue.$refs && 'nppfs-loading' in vue.$refs)
      vue.$refs['nppfs-loading'].hide();
  }

  start() {
    const _this = this;
    const modules = _this.options;

    _this.intervalKeepAlive = setInterval(function () {
      //   function reload(result) {}

      function pad(n, width) {
        n = n + '';
        return n.length >= width
          ? n
          : new Array(width - n.length + 1).join('0') + n;
      }

      let products = 0;
      if (modules.npn) {
        products += 1;
      }
      if (modules.npk) {
        products += 2;
      }
      if (modules.npf) {
        products += 4;
      }
      if (modules.kpd) {
        products += 8;
      }

      products = pad(products, 4);

      const random = npCommon.getRandomBytes(16);
      const param = [];
      param.push(
        npCommon.hexEncode(random) +
          npCommon.encrypt(
            products,
            npCommon.hexDecode(npCommon.hexEncode(random)),
            'ECB',
            128,
          ),
      );

      let parameter = param.length.toString(16); // 파라메터개수
      for (let i = 0; i < param.length; i++) {
        const value = param[i];
        parameter += npCommon.makeLength(value) + value;
      }

      const command = npCommand.makeCommand(
        npPacket.PRODUCT_DM,
        npPacket.MODE_SYNC,
        npPacket.CMD_KEEP_ALIVE,
        parameter,
      );

      let promise = null;
      if (npDefine.win) {
        promise = _this.commandInstance.doCommand(command);
      } else {
        promise = _this.commandInstance.doCommand(command);
      }

      promise.then(function (response) {
        // const result = response.data;
        try {
          
        } catch (e) {
          //
        }
      });
    }, 3000);

    return Promise.resolve();
  }

  destory() {
    clearInterval(this.intervalKeepAlive);
  }

  init() {
    // console.log("npBroadCast, UUID", this.uuid);
    const _this = this;
    return new Promise(function (resolve, reject) {
      (function () {
        return new Promise(function (resolve, reject) {
          _this.showLoading();
          resolve();
        });
      })()

        .then(function () {
          return new Promise(function (resolve, reject) {
            if (_this.initialized) {
              resolve();
              return;
            }
            if (_this.options.kpd || _this.options.npd) {
              const params = {
                p: 'p',
                m: 'p',
                u: _this.uuid,
              };

              npCommon
                .post(params)
                .then((data) => {
                  // console.log("Keypad Key : ", data);

                  const rsa = npCommon.parsePublicKey(data);
                  _this.keyset.seedkey = rsa.seedkey;
                  _this.keyset.rsakey = rsa.rsakey;
                  _this.keyset.rsa = rsa.rsa;
                  _this.rsamessage = rsa.message;

                  resolve();
                })
                .catch((err) => {
                  // console.log("Error Keypad Key : ", err);
                  reject(err);
                });
            } else {
              resolve();
            }
          });
        })

        .then(function () {
          return new Promise(function (resolve, reject) {
            if (_this.initialized) {
              resolve();
              return;
            }
            if (
              (_this.options.kpd || _this.options.npk) &&
              _this.options.vendor !== ''
            ) {
              const params = {
                p: 'p',
                m: 'p',
                u: _this.uuid,
                c: _this.options.vendor,
              };

              npCommon
                .post(params)
                .then((data) => {
                  // console.log("Keypad Key : ", data);

                  const rsa = npCommon.parsePublicKey(data);
                  _this.keyset.seedkey3rd = rsa.seedkey;
                  _this.keyset.rsakey3rd = rsa.rsakey;
                  _this.keyset.rsa3rd = rsa.rsa;

                  resolve();
                })
                .catch((err) => {
                  if (npConfig.DEBUG_MODE)
                    console.log('Error Keypad Key : ', err);
                  reject(err);
                });
            } else {
              resolve();
            }
          });
        })
        
        .then(function () {
          return new Promise(function (resolve, reject) {
            if (_this.initialized) {
              resolve();
              return;
            }
            if (_this.options.kpd) {
              _this.isAbsoluteUse(function (res) {
                _this.absoluteUse = res;
                resolve();
              });
            } else {
              resolve();
            }
          });
        })

        .then(function () {
          return new Promise(function (resolve, reject) {
            if (_this.initialized) {
              resolve();
              return;
            }

            if (!_this.options.kpd) {
              resolve();
            }

            if (!npConfig.KEYPAD['outside-hide']) {
              resolve();
            }

            // jQuery UI의 Slider를 사용할 경우 이벤트가 충돌하여
            // 키패드 외부 마우스 클릭 시 키패드 닫아주는 기능이 정상동작하지 않음.
            // jQuery UI의 Slider가 있는지 확인하여 해당기능 활성/비활성화 by YGKIM @2016.10.27 with 김준희
            const sliders = document.getElementsByClassName('ui-slider');
            
            if (sliders.length > 0) {
              resolve();
            }

            function findAncestor(el, sel) {
              while (
                (el = el.parentElement) &&
                !(
                  el.matches ||
                  el.matchesSelector ||
                  el.msMatchesSelector
                ).call(el, sel)
              );
              return el;
            }
            function hasClass(el, clazz) {
              return (' ' + el.className + ' ')
                .replace(/[\n\t]/g, ' ')
                .includes(' ' + clazz + ' ');
            }

            const doc = document.body;
            const eventkind = npDefine.isMobileDevice()
              ? { start: 'touchstart', move: 'touchmove', end: 'touchend' }
              : { start: 'mousedown', move: 'mousemove', end: 'mouseup' };

            doc.addEventListener(eventkind.start, function (event) {
              const target = event.target;
              const parent = findAncestor(target, '.nppfs-keypad-div');
              
              if (parent || hasClass(target, 'nppfs-npv')) {
                return;
              }

              function handler(evt) {
                const target = evt.target;
                const parent = findAncestor(target, '.nppfs-keypad-div');

                if (parent || hasClass(target, 'nppfs-npv')) {
                  doc.removeEventListener(eventkind.end, handler);
                  return;
                }

                if (evt.type === eventkind.end) {
                  const keypad = _this.modules.keypad;
                  if (!npConfig.KEYPAD['outside-close']) {
                    if (keypad) keypad.closeAll();
                  } else if (keypad) keypad.hideAll();
                } else {
                  
                }

                doc.removeEventListener(eventkind.end, handler);
              }
              doc.addEventListener(eventkind.end, handler);
            });

            // 마우스입력기 초기화 완료 속성값 변경
            _this.initialized = true;
            resolve();
          });
        })

        // 마우스입력기 시작
        .then(function () {
          if (_this.options.kpd) {
            const keypad = new npKeypad(_this, {
              uuid: _this.uuid,
              keyset: _this.keyset,
              absoluteUse: _this.isAbsoluteUse(),
            });
            if (!keypad.isSupport()) return Promise.resolve();

            _this.modules.keypad = keypad;
            return keypad.init(); // 초기화
          } else return Promise.resolve();
        })

        .then(function () {
          _this.hideLoading();

          console.log('모듈 실행이 완료되었습니다.');

          resolve();
        })
        .catch((err) => {
          console.log('Error : ', err);
          _this.hideLoading();
          reject(err);
        });
    });
  }

  components() {
    const _self = this;
    if (_self.options.kpd) {
      return _self.modules.keypad ? _self.modules.keypad.componentsArray() : [];
    }
    return [];
  }

  toJSON() {
    let json = {};
    // console.log('============= ', this.modules);
    for (const key in this.modules) {
      const module = this.modules[key];
      json = Object.assign(json, module.toJSON() || {});
    }
    return json;
  }

  /************************************************************
   * 가상키패드 강제사용정책 적용
   ***********************************************************/
  isAbsoluteUse(callback) {
    const _self = this;
    let useyn = true;

    if(callback && typeof callback === "function") {
			callback(useyn);
		}

    return useyn;
  }

  table(id) {
    const _self = this;
    const keypad = _self.modules.keypad;
    if (keypad && keypad.isRunning && keypad.isUseYn(id)) {
      return keypad.table(id);
    }

    return Promise.reject(new Error('실행중인 보안입력 기능이 없습니다.'));
  }

  replacement(id) {
    const _self = this;
    const keypad = _self.modules.keypad;
    if (keypad && keypad.isRunning && keypad.isUseYn(id)) {
      return keypad.replacement(id);
    }

    return Promise.reject(new Error('실행중인 보안입력 기능이 없습니다.'));
  }

  encrypt(id) {
    const _self = this;
    const keypad = _self.modules.keypad;
    if (keypad && keypad.isRunning && keypad.isUseYn(id)) {
      return keypad.encrypt(id);
    }

    return Promise.reject(new Error('실행중인 보안입력 기능이 없습니다.'));
  }

  destroy(id) {
    const _self = this;

    const keypad = _self.modules.keypad;
    if (keypad && keypad.isRunning) {
      keypad.destroy(id);
    }
  }

  finalize() {
    
  }

  inputFocusOutSync() {
    
  }

};

export default npController;
